.tooltipRoot {
  width: 100%;
  height: 100%;
  display: flex;

  [data-hook='popover-element']{
    width: 100%;
    height: 100%;
  }
}

.iconContainer {
  position: relative;
}

.standardSkin:hover {
  [data-hook='add-panel-item-icon-container']{
    background-color: var(--wsr-color-B50) !important;
    border-color: var(--wsr-color-B40) !important;
    color: var(--wsr-color-B10);
  }
}

.selectedItem {
  position: absolute;
  top: -12px;
  left: 20px;
  z-index: 1;
  width: 16px;
  height: 16px;
  background-color: var(--wsr-color-D80);
  border: 1px solid;
  border-color: var(--wsr-color-D60) !important;
  border-radius: 15px;
  align-items: center;
  vertical-align: middle;

  svg {
    color: var(--wsr-color-D10) !important;
  }

}

.disabled {
  pointer-events: none;
  border-color: rgba(0, 6, 36, 0.1) !important;

  svg {
    color: rgba(0, 6, 36, 0.3) !important;
  }
}

.itemText {
  text-align: center;
}
