.option {
    border-radius: 12px;
    padding: 3px 12px;
    margin: 3px 0;
    display: flex;
    align-items: center;
    gap: 8px;
    max-width: calc(100% - 24px);
}

.ellipsis {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.collapsed {
    max-width: 176px;
}

.option:not(:last-child) {
    margin-right: 6px;
}

.defaultColor {
    background-color: #DAEFFE;
}

.otherColor {
    background-color: #ECEFF3;
}

.deletedColor {
    background-color: #FFFFFF;
    border: 1px solid #DFE5EB;
}

.collapsedDeleted {
    max-width: 158px;
}

.hover {
    cursor: pointer;
}

.infoIcon {
    color: #000624;
}

.img {
  cursor: pointer;
  width: 18px;
  height: 18px;
  margin-left: -6px;
}

.wordWrap {
  width: 100%;
  display: inline-block;
  word-wrap: break-word !important;
}
  