.list, 
.list li {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style-type: none;
}

.list {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.product {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, auto);
  column-gap: 18px;
  row-gap: 6px;
}

.title {
  grid-column: span 3 / span 3;
}

.column {
  display: grid;
  gap: 6px;
}

.columnTitle {
  color: #868AA5;
}

.divider {
  width: 100%;
  height: 1px;
  margin-block: 9px;
  background-color: #DFE5EB;
}